import { BASE_URL, INACTIVE_TIME_IN_SECONDDS } from "../config.js";
import axios from "axios";
import { authoreizeAxios } from "./app-http";
import { differenceInSeconds } from "date-fns";

export default {
    login(user) {
        return axios
            .post(
                `${BASE_URL}/login`,
                {
                    username: user.username,
                    password: user.password
                },
                { crossDomain: true }
            )
            .then((resp) => {
                localStorage.setItem(
                    "user",
                    JSON.stringify({
                        id: resp.data.logged_user.id,
                        user: user.username,
                        accessToken: resp.data.api_key,
                        username: resp.data.logged_user.username,
                        name: resp.data.logged_user.name,
                        email: resp.data.logged_user.email,
                        providerNo: resp.data.logged_user.providerNo,
                        providerType: resp.data.logged_user.providerType,
                        permissions: resp.data.logged_user.permissions
                    })
                );
                localStorage.setItem(
                    "needSecondVerification",
                    resp.data.second_level_varification
                );

                authoreizeAxios();

                return {
                    user: user.username,
                    accessToken: resp.data.api_key,
                    userDetail: resp.data.logged_user,
                    needSecondVerification: resp.data.second_level_varification
                };
            });
    },

    logout() {
        localStorage.removeItem("user");
        localStorage.removeItem("PATIENT");
        localStorage.removeItem("PARTNER");
        localStorage.removeItem("CYCYLE_SUMMARY");
        localStorage.removeItem("SELECTED_CYCLE");
        localStorage.removeItem("needSecondVerification");
    },

    forceLogoutIfInactive() {
        const lastLoginStr = localStorage.getItem("LAST_ACTIVE");

        if (lastLoginStr === null) {
            localStorage.setItem("LAST_ACTIVE", new Date().toString());
        } else {
            const diff = differenceInSeconds(
                new Date(),
                new Date(lastLoginStr)
            );

            if (diff < INACTIVE_TIME_IN_SECONDDS) {
                localStorage.setItem("LAST_ACTIVE", new Date().toString());
            } else {
                localStorage.removeItem("LAST_ACTIVE");
                this.logout();
                location.reload();
            }
        }
    },

    register(user) {
        return axios.post(BASE_URL + "signup", {
            username: user.username,
            email: user.email,
            password: user.password
        });
    }
};
